import mapboxgl from "mapbox-gl";
import 'mapbox-gl/dist/mapbox-gl.css';
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
import { CONTACT_US } from "../../api/api";
export default {
  name: "us",

  data() {
    return {
      accessToken: process.env.VUE_APP_MAP_BOX_ACCESS_TOKEN,
      mapStyle: process.env.VUE_APP_MAP_BOX_STYLE,
      usForm: {
        first_name: '',
        last_name: '',
        email: '',
        message: ''
      },
      usRules: {
        first_name: [{
          required: true,
          message: 'Please enter your first name',
          trigger: 'blur'
        }],
        last_name: [{
          required: true,
          message: 'Please enter your last name',
          trigger: 'blur'
        }],
        email: [{
          required: true,
          message: 'Please enter your email',
          trigger: 'blur'
        }],
        message: [{
          required: true,
          message: 'Please enter your message',
          trigger: 'blur'
        }]
      },
      submitLoadingStatus: false
    };
  },

  mounted() {
    this.initMap(-98.33076300, 29.65546900);
  },

  methods: {
    initMap(lng, lat) {
      mapboxgl.accessToken = this.accessToken;
      const map = new mapboxgl.Map({
        container: "mapContainer",
        center: [lng, lat],
        style: this.mapStyle,
        zoom: 12
      });
      const nav = new mapboxgl.NavigationControl();
      map.addControl(nav, "top-right");
      const geolocate = new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true
        },
        trackUserLocation: true
      });
      map.addControl(geolocate, "top-right");
      const geocoder = new MapboxGeocoder({
        "accessToken": this.accessToken,
        "mapboxgl": mapboxgl
      });
      map.addControl(geocoder, 'top-left');
      const marker = new mapboxgl.Marker();
      marker.setLngLat([lng, lat]).addTo(map);
    },

    submitForm(formName) {
      this.submitLoadingStatus = true;
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.usForm);
          CONTACT_US(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              this.$message.success('Success');
              this.submitLoadingStatus = false;
            }
          }).catch(err => {
            console.log(err);
            this.$message.error(err.msg);
          });
          console.log('submit');
        } else {
          console.log('error submit!!');
          this.submitLoadingStatus = false;
          return false;
        }
      });
    }

  }
};